import styled from "styled-components";
import background1 from '../assets/images/Home/background1.png'
import background2 from '../assets/images/Home/background2.png'
import image1 from '../assets/images/Home/image1.png'
import image2 from '../assets/images/Home/image2.png'
import Logo from '../assets/images/Navbar/PUREVER.svg'
import { Navbar } from "../components/Navbar/Navbar";
import { useNavigate } from 'react-router-dom';
import { isLoggedIn } from "../api/isLoggedIn";

const BackgroundImgContainer1 = styled.div`
    background-image: url(${background1});
    width: 100vw;
    height: 1080px;
    background-size: cover;
    background-position: center;
`
const TitleTextContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 35px;
    margin-right: 14%;
    margin-top: 120px;
`
const TitleText = styled.div`
    text-align: ${({ $align }) => ($align === 'left' ? 'left' : 'right')};
    font-size: 64px;
    font-weight: 800;
    line-height: 110%;
    letter-spacing: -0.64px;
`
const SubText = styled.div`
    text-align: right;
    font-size: 24px;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: -0.24px;
`
const BackgroundColorContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #E8ECF1;
    width: 100vw;
` 
const IntroContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 125px 0px;
`
const MainText = styled.div`
    text-align: ${({ $align }) => ($align === 'left' ? 'left' : 'right')};
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%;
    letter-spacing: -0.56px;
`
const ServiceInfoText = styled.div`
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    margin: 22px 0px 45px 0px;
    color: #212121
`
const MintBtn = styled.div`
    display: flex;
    padding: 28px 40px;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background-color: #69D1DE;
    cursor: pointer; 
    
    color: white;
    font-size: 24px;
    font-weight: 600;
    line-height: 110%;

    &:hover {
        background-color: #5cc3d3;
    }
`
const BackgroundContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 1430px;
    gap: 144px;
`
const FeatureInfoContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 560px;
    gap: 51px;
`
const FeatureInfoText = styled.div`
    text-align: ${({ $align }) => ($align === 'left' ? 'left' : 'right')};
    color: #494B56;
    font-size: 24px;
    font-weight: 600;
    line-height: 36px;
    letter-spacing: 0.2px;
    text-transform: capitalize;
`
const ImgContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`
const Img = styled.img`
    box-shadow: 0px 30px 60px 0px rgba(122, 127, 131, 0.20);
    border-radius: 25px;
`
const ExpertContainer = styled.div` //  
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 78px;
    flex-shrink: 0;
    padding: 151px 0px;
`
const MintText = styled.span`
    color: #69D1DE;
`
const QuoteContainers = styled.div`
    display: flex;
    align-items: center;
    gap: 187px;
`
const Quote = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
`
const DoctorText = styled.div`
    color: #6E6E73;
    font-feature-settings: 'clig' off, 'liga' off;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.46px;
`
const QuoteText = styled.div`
    color: #212121;
    font-feature-settings: 'clig' off, 'liga' off;
    font-size: 24px;
    font-weight: 500;
    letter-spacing: 0.46px;
    margin: 22px 0px 37px 0px;
`
const DetailText = styled.a`
    text-decoration-line: none;
    color: #494B56;
    font-feature-settings: 'clig' off, 'liga' off;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0.46px;
    cursor: pointer;
`
const BackgroundImgContainer2 = styled.div`
    background-image: url(${background2});
    width: 100vw;
    height: 607px;
    background-size: cover;
    background-position: center;
`
const LogoImg = styled.img`
    width: 270px;
    height: 45px;
    margin: 122px auto 116px auto;
`
const FooterContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 1083px;
`
const CompanyText = styled.div`
    color: #494B56;
    font-size: 16px;
    font-weight: 500;
    line-height: 28px;
    margin-left: auto;
`
const CopyrightText = styled.div`
    color: #6E6E73;
    font-size: 16px;
    font-weight: 400;
    line-height: 18px;
    margin-bottom: 88px;
`
const GreyLine = styled.hr`
    background: rgba(0, 0, 0, 0.32);
    margin: 18.5px 0px 18.5px 0px;
`
const Home = () => {
  const navigate = useNavigate(); //    useNavigate  

    const handleStartClick = () => { //     
        const state = isLoggedIn();

        if(state)
            navigate('/memory'); //      
        else
            navigate('/login'); //     
    };

    return(
        <>
        <BackgroundImgContainer1>
            <Navbar/>
            <TitleTextContainer>
                <TitleText $align='right'>   <br/>  </TitleText>
                <SubText>       <br/>     </SubText>
            </TitleTextContainer>
        </BackgroundImgContainer1>


        <BackgroundColorContainer>
            <IntroContainer>
                <MainText>   .</MainText>
                <ServiceInfoText>
                               <br/>
                    (Well-being)   (well-dying)  .
                </ServiceInfoText>
                <MintBtn onClick={handleStartClick}>  </MintBtn> {/*      */}
            </IntroContainer>
        </BackgroundColorContainer>


        <BackgroundContainer>
            <ImgContainer>
                <FeatureInfoContainer>
                    <MainText $align='left'>  <br/> </MainText>
                    <FeatureInfoText $align='left'>
                             .<br/>
                           <br/>
                            .<br/>
                        <br></br>
                               .
                    </FeatureInfoText>
                </FeatureInfoContainer>
                <Img src={image1} alt="img1"/>
            </ImgContainer>
            <ImgContainer>
                <Img src={image2} alt="img2"/>
                <FeatureInfoContainer>
                    <MainText $align='right'>   <br/> </MainText>
                    <FeatureInfoText $align='right'>
                            .<br/>
                             <br/>
                        <br></br>
                            .
                    </FeatureInfoText>
                </FeatureInfoContainer>
            </ImgContainer>
        </BackgroundContainer>


        <BackgroundColorContainer>
            <ExpertContainer>
                <TitleText $align='left'>    <br/> <MintText></MintText>  </TitleText>
                <QuoteContainers>
                    <Quote>
                        <DoctorText>   </DoctorText>
                        <QuoteText>
                                <br/>
                               <br/>
                               . <br/>
                        </QuoteText>
                        <DetailText href="https://www.mk.co.kr/news/it/10559989" target="_blank"> </DetailText>
                    </Quote>
                    <Quote>
                        <DoctorText> </DoctorText>
                        <QuoteText>
                              , <br/>
                                <br/>
                             . <br/>
                        </QuoteText>
                        <DetailText href="https://www.hankyung.com/article/2024022718057" target="_blank"> </DetailText>
                    </Quote>
                    <Quote>
                        <DoctorText>  </DoctorText>
                        <QuoteText>
                                 <br/>
                                 <br/>
                              ... <br/>
                        </QuoteText>
                        <DetailText href="https://www.hani.co.kr/arti/animalpeople/companion_animal/1027755.html" target="_blank"> </DetailText>
                    </Quote>
                </QuoteContainers>
                <MintBtn onClick={handleStartClick}>  </MintBtn> {/*      */}
            </ExpertContainer>
        </BackgroundColorContainer>


        <BackgroundImgContainer2/>


        <BackgroundColorContainer>
            <FooterContainer>
                <LogoImg src={Logo}/>
                <CompanyText>   12 </CompanyText>
                <GreyLine/>
                <CopyrightText>Copyright  2024 Likellion Hongik. All Rights Reserved.</CopyrightText>
            </FooterContainer>
        </BackgroundColorContainer>
        </>
    );
}

export default Home;